import React from 'react';
import './Quicksearch.css';
import {Link} from 'react-router-dom';

const QuickDisplay = (props) => {
    const renderMealtype = ({quickdata}) => {
        if(quickdata){
            return quickdata.map((item)=>{
                return(
                <Link to={`/list/${item.mealtype}`} key={item._id}>
                    <div className="title-container">
                        <div className="tilecomponent1">
                            <img src={`./images/${item.name}.png`} className="imageclass"/>
                        </div>
                        <div className="tileComponent2">
                            <div className="componentHeading">
                                {item.name}
                            </div>
                            <div className="componentSubHeading">
                                Start your day with exclusive {item.name} options
                            </div>
                        </div>
                    </div>
                </Link>
                )
            })
        }
        else{
            return(
                <div>
                    <img src="/images/loader.gif" className="img-responsive loader"/>
                </div>
            )  
        }
    }
    return(
    <React.Fragment>
        <div className="quicksearch">
            <div>
                <p className="quicksearch-heading">Quick Searches</p>
                <p className="quicksearch-subheading">Discover restaurants from type of meal</p>
            </div>
            {renderMealtype(props)}
        </div>
    </React.Fragment>
    )
}

export default QuickDisplay;