import React, { Component } from 'react'
import axios from 'axios'


const costurl="https://developer-restaurant.herokuapp.com/restaurantList";
class Costfilter extends Component {

    costfiltering = (event) => {
     let  costype=(event.target.value).split(",");
     let lcost = Number(costype[0]);
     let hcost = Number(costype[1]);
      let  mealtype=sessionStorage.getItem('mealid');
      let curl;
      if(costype == ''){
          curl=`${costurl}/${mealtype}`
      }
      else{
       curl=`${costurl}/${mealtype}?lcost=${lcost}&&hcost=${hcost}`
      }
   
      axios.get(curl)
      .then((response)=>{this.props.costdata(response.data)})

    }
    render(){
        return(
            <React.Fragment>
                <div className="filter" onChange={this.costfiltering}>
                    <h4>Cost For Two</h4>
                    
                    <label className="radio">
                        <input type="radio" value="" name="filter"/>All
                    </label>
                   
                    <label className="radio">  
                    <input type="radio" name="filter" value='0,300'/>Less than 300
                    </label>
                    
                    <label className="radio">
                        <input type="radio" name="filter" value='300,500'/>₹300 to ₹500
                    </label>
                    
                    <label className="radio">
                        <input type="radio" name="filter" value='500,1000'/>₹500 to ₹1000
                    </label>
                   
                    <label className="radio">  
                        <input type="radio" name="filter" value='1000,2000'/>₹1000+
                    </label>
                </div>
            </React.Fragment>
        )
    }
}
export default Costfilter