import axios from 'axios';
import React, { Component } from 'react'

const cuisineurl="https://developer-restaurant.herokuapp.com/restaurantList";


class Cuisinefilter extends Component {
    cuisinefiltering = (event) => {
        let cuisinetype=(event.target.value);
        let mealtype = sessionStorage.getItem('mealid');
        let curl;
        if(cuisinetype == ''){
            curl=`${cuisineurl}/${mealtype}`
        }
        else{
         curl=`${cuisineurl}/${mealtype}?cuisine=${cuisinetype}`
        }
     
        axios.get(curl)
        .then((response)=>{this.props.cuisinedata(response.data)})
     }
    render(){
        return(
            <React.Fragment>
                <div className="filter" onChange={this.cuisinefiltering}>
                    <h4>Cuisine</h4>

                    <label className="radio">
                     <input type="radio" name="filter"value=''/>All
                    </label>
                    
                    <label className="radio">
                     <input type="radio" name="filter"value='1'/>North Indian
                    </label>
                   
                    <label className="radio"> 
                     <input type="radio" name="filter"value='2'/>South Indian
                    </label>
                    
                    <label className="radio">
                        <input type="radio" name="filter"value='3'/>Chinese
                    </label>
                    
                    <label className="radio">
                        <input type="radio" name="filter"value='4'/>FastFood
                    </label>
                   
                    <label className="radio">  
                        <input type="radio" name="filter" value='5'/>Streetfood
                    </label>
                </div>
            </React.Fragment>
        )
    }
}
export default Cuisinefilter