import React from 'react';
import {Link} from 'react-router-dom';

import {withRouter} from 'react-router-dom';

const deleteorders="https://developer-restaurant.herokuapp.com/deleteorders";



const BookingDisplay =(props) =>{
    console.log(props)

 

   


    const renderTable = ({quickorder}) =>{
        if (quickorder){
            return  quickorder.map((item)=>{

               
                  const  handleDelete  = () => {
                    fetch(deleteorders,{
                        method: 'delete',
                        headers: { 'Content-Type': 'application/json' },
                        body: JSON.stringify({
                          order_id:item.order_id
                        })
                      })
                      .then(response => response.json())
                      .then(data => console.log(data)) 
                      .catch(err => console.log(err))
                       
                    
                 }

                return(
                    <tr key={item._id}>
                        <td>{item.order_id}</td>
                         <td>{item.rest_name}</td>
                         <td>{item.name}</td>
                         <td>{item.phoneno}</td>
                         <td>{item.address}</td>
                         <td>{item.persons}</td>
                         <td><button onClick={handleDelete} className="btn btn-danger btn-trash"><span><i className="	glyphicon glyphicon-trash"></i></span></button></td>
                    </tr>
                )
            })
        }
  
        else{
            return(
                <div>
                    <img src="/images/loader.gif" className="img-responsive loader"/>
                </div>
            )  
        }

       
    }
    
    return(
        <div className="container">
           <alert className="alert" style={{color:"green"}}>
                {(props.location.search).split('=')[1]}
            </alert>                             
            <center><h3>Orders Details</h3></center>
            <table className="table table-responsive">
                <thead>
                    <tr>
                        <th>Order Id</th>
                        <th>Rest Name</th>
                        <th>Name</th>
                        <th>Phone</th>
                        <th>Address</th>
                        <th>No. Person</th>
                        <th>Status</th>
                    </tr>
                </thead>
                <tbody>
                    {renderTable(props)}
                </tbody>
            </table>
        </div>
    )
}

export default withRouter (BookingDisplay)