import React, { Component } from 'react';
import axios from 'axios';
import './RestaurantList.css'
import Cuisinefilter from '../Filter/Cuisinefilter'
import Costfilter from '../Filter/Costfilter'
import Sortfilter from '../Filter/Sortfilter'
import ListDisplay from './ListingDisplay'
import Suggestionapi from './Suggestionapi'



const mrurl = 'https://developer-restaurant.herokuapp.com/restaurants?mealtype=';
const mealurl = 'https://developer-restaurant.herokuapp.com/mealtype';

var limit = 2
class ListingApi extends Component {
    constructor(props){
        super() 
        this.state={
            hotelist:'',
            mealtype:'',
            activePage: 1,
            totalNoOfItems: 1,
        }
    }

    setDataByFilter(sortedData){
        this.setState({hotelist:sortedData})
    }
    render(){
       
        return(
        <div className="container">
           
           <div className="row">
                <div className="filter col-md-3 col-sm-3">
                    <div className="filterhead">
                        <div className="">
                            <nav className="navbar navbar-inverse" id="filter1" >
                                <div className="container-fluid">
                                    <div className="navbar-header">
                                        <button className="navbar-toggle" data-toggle="collapse" id="downarrow" data-target="#myNavfilter">
                                            <span  className="glyphicon glyphicon-arrow-down"></span>
                                        </button>
                                        <div className="navbar-brand" id="filterbrand">Filters</div>
                                     </div>
                                    <div className="collapse-navbar-collapse" id="myNavfilter">
                                        <ul className="nav navbar-nav">
                                            <li>  <Cuisinefilter cuisinedata = {(data) => {this.setDataByFilter(data)}} /></li>
                                            <li><Costfilter costdata = {(data) => {this.setDataByFilter(data)}}/></li>
                                            <li><Sortfilter sortdata = {(data) => {this.setDataByFilter(data)}}/></li>
                                        </ul>
                                    </div>    
                                </div>
                            </nav>
                        </div>
                    </div> 
                </div>
               
              
               
                <div className="col-md-8 col-md-offset-1 col-sm-8 col-sm-offset-1" >
                    <div className="row">
                        <div className="suggest">
                        <Suggestionapi/>
                        </div>
                    </div>
                    <div className="listheading">
                        <h2>BEST RESTAURANTS FOR {this.state.mealtype}</h2>
                    </div>
                   
                   <ListDisplay quicklist = {this.state.hotelist} activePage={this.state.activePage} totalNoOfItems={this.state.totalNoOfItems} limit={limit} pageNumber={(data)=>{this.setState({activePage:data})}}/>
                </div>
               
            </div>
        </div>
        )
    }

    componentDidMount(){
        var mealid = this.props.match.params.id;
        sessionStorage.setItem('mealid',mealid)
        axios.get(`${mealurl}`)
        .then((response) => {
            for(var i=0;i<response.data.length;i++){
             if(response.data[i]._id === Number(mealid)){
                 this.setState({mealtype:(response.data[i].name).toUpperCase()})
                 i = response.data.length;
             }else{
                 this.setState({mealtype:'Check your input'})
             }
            }
           
        })
        fetch(`${mrurl}${mealid}`)
        .then((res) => res.json())
       .then((data) =>  {this.setState({
            hotelist: data.slice(0, data.length - 1),
            totalNoOfItems:data.length - 1
        })})
    }

}
export default ListingApi;