import React, { Component } from 'react';
import './Search.css'
import {withRouter} from 'react-router-dom'
 
const lurl = 'https://developer-restaurant.herokuapp.com/location';
const rurl = 'https://developer-restaurant.herokuapp.com/restaurants?city=';
class Search extends Component{
    constructor(){
        super()
            this.state={
                location:'',
                restaurant:''
            }
    }
//Display city data//
    renderCity = (data) =>{
        if(data){
            return data.map((item) => {
                return(
                    <option value={item.city} key={item._id} >{item.name} | {item.city_name}</option>
                )
            })
        }
    }
//Display restaurant data//
    renderRestaurant = (data) => {
        if(data){
            return data.map((item)=>{
                return(
                    <option value={item._id} key={item._id}>{item.name} | {item.locality}</option>
                )
            })
        }
    }
//restaurant with respect to city//
    handleCity = (event) =>{
        const cityid = event.target.value;
        const rrurl =`${rurl}${cityid}`
        fetch(rrurl,{Method:'GET'})
        .then((res) => res.json())
        .then((data) => {this.setState({restaurant:data})})
    }

    handleRest =(event) => {
        console.log(event.target.value)
        this.props.history.push(`/details/${event.target.value}`)
    }
   

    render(){
        return(
        <div className="image-container">
            <br/>
            <div id="logo">
                e!
            </div>
            <div className="heading">
                Find the best restaurants, cafes, and bars
            </div>
            <div className="location-container">
                <select className="locationDropDown" onChange={this.handleCity}>
                    <option>----------SELECT CITY-----------</option>
                    {this.renderCity(this.state.location)}
                </select>
                <select className="locationDropDown" onChange={this.handleRest}>
                    <option>----SELECT RESTAURENTS----</option>
                    {this.renderRestaurant(this.state.restaurant)}
                </select>
            </div>
        </div> 
        )
    }
//call city Api on load//
    componentDidMount(){
        fetch(lurl,{Method:'GET'})
        .then((res) => res.json())
        .then((data) => {this.setState({location:data})})
    }
}


export default withRouter(Search)