import React from 'react';
import {Link} from 'react-router-dom';
import './suggestion.css';

const Suggestiondisplay = (props) => {

    const renderList =({suggestdata}) => {
        if(suggestdata){
            return suggestdata.map((item) => {
                return(
                   
                    <div className='card ' key={item._id}>
                        <h3 className="card-name"><Link to={`/details/${item._id}`}>{item.name}</Link></h3>
                        <img className='card-img-top' src={item.thumb[0].image}/>
                        <div className='card-body'>
                            <h4 className='card-title'>{item.locality}</h4>
                            <div className='card-text'>
                                <p>Cost: {item.cost}</p>
                            </div>
                        </div>
                    </div>
                )
            })
        }

    }
    return(
        <div className="col-md-12">
            <h3 className="suggest">Thinking About Pocket on {new Date().getDate()}. We have Pocket Friendly suggestion for you.</h3>
            <div className="">
                {renderList(props)}
            </div>
        </div>
    )
}

export default Suggestiondisplay;