import React , {Component} from 'react';
import axios from 'axios';
import BookingDisplay from './BookingDisplay';

const ourl="https://developer-restaurant.herokuapp.com/orders";

class BookingApi extends Component {
    constructor(){
        super()
        this.state={
            order:''
        }
    }
    render(){
        return(
            <div className="container">
                <BookingDisplay quickorder={this.state.order}/>
            </div>
        )
    }

    componentDidMount(){
        axios.get(`${ourl}`)
        .then((response) => {this.setState({order:response.data})})
    }

}
export default BookingApi