import axios from 'axios';
import React , { Component }  from 'react';
import {Link} from 'react-router-dom';
import './Bookingform.css'

const resturl="https://developer-restaurant.herokuapp.com/restaurantdetails";
const placeorder="https://developer-restaurant.herokuapp.com/placeorder";

class Placeorder extends Component{
    constructor(){
        super()
        this.state={
            order_id:Math.floor(Math.random()*10000),
            rest_name:'',
            name:'',
            phoneno:'',
            address:'',
            persons:''

        }
    }

    handleChangename = (event) => {
        this.setState({name:event.target.value})
    }

    handleChangephone = (event) => {
        this.setState({phoneno:event.target.value})
    }

    handleChangeaddress = (event) => {
        this.setState({address:event.target.value})
    }

    handleChangeperson = (event) => {
        this.setState({persons:event.target.value})
    }

    handleSubmit = () => {
        fetch(placeorder,{
            method:'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(this.state)
        })
        .then((this.props.history.push('/vieworder?message=Success')))
     }

     
    render(){
        return(
            <div className="container">
                <div className="panel panel-primary">
                    <div className="panel-heading">
                        <h3>Place Order</h3>
                    </div>
                    <div className="Panel-body">
                        <div className="form-group">
                            <div>
                                <label>Order_Id</label>
                                <input type="text" name="order_id" value={this.state.order_id} readOnly className="form-control"/>
                            </div>
                            <div>
                                <label>Restaurant</label>
                                <input type="text" name="rest_name" value={this.state.rest_name} readOnly className="form-control"/>
                            </div>
                            <div>
                                <label>Name</label>
                                <input type="text" name="name"              onChange={this.handleChangename}
                                value={this.state.name} className="form-control"/>
                            </div>
                            <div>
                                <label>Address</label>
                                <input type="text" name="address" onChange={this.handleChangeaddress} value={this.state.address} className="form-control"/>
                            </div>
                            <div>
                                <label>Phone No</label>
                                <input type="text" name="phoneno" onChange={this.handleChangephone} value={this.state.phoneno} className="form-control"/>
                            </div>
                            <div>
                                <label>No.of.persons</label>
                                <select type="text" name="persons" onChange={this.handleChangeperson} value={this.state.persons} className="form-control">
                                    <option value="default">---Select---</option>
                                    <option value="1">1</option>
                                    <option value="2">2</option>
                                    <option value="3">3</option>
                                    <option value="4">4</option>
                                    <option value="5">5</option>
                                    <option value="6">6</option>
                                    <option value="7">7</option>
                                    <option value="8">8</option>
                                    <option value="9">9</option>
                                    <option value="10">10</option>
                                </select>
                            </div>
                        </div>
                        <div className="formbtn">
                            <Link to={`/details/${this.props.match.params.id}`} className="btn btn-danger">Back</Link>&nbsp;
                            <button className="btn btn-success" onClick={this.handleSubmit}>Submit</button> 
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    componentDidMount(){
        var restname=this.props.match.params.id
        console.log(restname);
        axios.get(`${resturl}/${restname}`)
        .then((response)=>{this.setState({rest_name:response.data[0].name})})
    }
}
export default Placeorder
