import React from 'react';
import {Link} from 'react-router-dom';
import Dish from '../Dish/Dishapi'
import MapContainer from '../googlemap'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';



const RestaurantDisplay = (props) =>{
   
    const renderResdetail = ({quickdetail}) =>{
       
        if(quickdetail){
            return(
                <div className="container">
                    <div className="panel panel-primary">
                        <div className="panel-heading">
                            <div className="head"> 
                                <p>{quickdetail.name}</p>
                            </div>
                        </div>
                        <div className="panel-body">
                            <div className="detailimage">
                                <div>
                                   <img className="img-responsive" src={`${quickdetail.thumb[0].image}`}/>
                                    <button type="button" className="btn btn-lg" data-toggle="modal" data-target="#myModal">    
                                        <span><i className="glyphicon glyphicon-picture"></i>&nbsp; Image Gallery</span>
                                    </button>     
                                </div> 
                                <div className="">
                                    <button type="button" className="btn mapbutton" data-toggle="modal" data-target="#Modalmap"> <span><i className="	glyphicon glyphicon-map-marker"></i></span> &nbsp; Direction</button>
                                    <div class="modal fade" id="Modalmap" role="dialog">
                                        <div className="modal-dialog">
                                            <div className="">
                                                <div class="modal-body map">
                                                    <MapContainer/>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>  
                                <div className="modal fade" id="myModal" role="dialog">
                                    <div className="modal-dialog">
                                        <div className="modal-content">
                                            <div className="modal-header">
                                                <button type="button" className="close" data-dismiss="modal">&times;</button>
                                                <h4 className="modal-title">{quickdetail.name}</h4>
                                            </div>
                                            <div className="modal-body">
                                                <div id="myCarousel" className="carousel slide" data-ride="carousel">

                                                    <ol className="carousel-indicators">
                                                        <li data-target="#myCarousel" data-slide-to="0" className="active"></li>
                                                        <li data-target="#myCarousel" data-slide-to="1"></li>
                                                        <li data-target="#myCarousel" data-slide-to="2"></li>
                                                    </ol>
                                                    <div className="carousel-inner">
                                                        <div className="item active">
                                                            <img className="img-responsive" src={quickdetail.thumb[0].image}
                                                            style={{width:1500,height:300}}/>
                                                        </div>

                                                        <div className="item">
                                                            <img className="img-responsive" src={quickdetail.thumb[1].image}
                                                            style={{width:1500,height:300}}/>
                                                        </div>

                                                        <div className="item">
                                                         <img className="img-responsive" src={quickdetail.thumb[2].image}
                                                            style={{width:1500,height:300}}/>
                                                        </div>
                                                    </div>
                                                    <a className="left carousel-control" href="#myCarousel" data-slide="prev">
                                                        <span className="glyphicon glyphicon-chevron-left"></span>
                                                        <span className="sr-only">Previous</span>
                                                    </a>
                                                    <a className="right carousel-control" href="#myCarousel" data-slide="next">
                                                        <span className="glyphicon glyphicon-chevron-right"></span>
                                                        <span className="sr-only">Next</span>
                                                    </a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        </div>
                        <div>
                            <Tabs>
                                <TabList>
                                    <Tab>Overview</Tab>
                                    <Tab>Contact</Tab>
                                    <Tab>Order</Tab>
                                </TabList>
                                <TabPanel>
                                    <div>
                                        <div className="about">
                                            About this place
                                        </div>
                                        <div>
                                            <p className="option">MealType Available</p>
                                            <p className="data">{quickdetail.type[0].name} | {quickdetail.type[1].name}</p>
                                        </div>
                                        <div>
                                            <p className="option">Cuisine</p>
                                            <p className="data">{quickdetail.Cuisine[0].name} | {quickdetail.Cuisine[1].name}</p>
                                        </div>
                                        <div>
                                            <p className="option">Average Cost</p>
                                            <p className="data">₹{quickdetail.cost} for two people (approx.)</p>
                                        </div>
                                    </div>
                                </TabPanel>
                                <TabPanel>
                                    <div>
                                        <div>
                                            <p className="option">Phone Number</p>
                                            <p className="data" style={{color:"#ce0505"}}>{quickdetail.contact_number}</p>
                                        </div>
                                        <div>
                                            <p className="option">{quickdetail.name}</p>
                                            <p className="data">{quickdetail.address}
                                            </p>
                                        </div>
                                    </div>
                                </TabPanel>
                                <TabPanel>
                                   <Dish/>
                                   
                                </TabPanel>
                            </Tabs>
                        </div>
                        <div className="row order">
                            <Link to={`/list/${sessionStorage.getItem('mealid')}`} className="btn btn-danger">Back</Link>&nbsp;&nbsp;
                            <Link to={`/booking/${quickdetail._id}`} className="btn btn-success">Place order</Link>
                            
                        </div>
                    </div>
                </div>
            )
        }
        else{
            return(
                <div>
                    <img src="/images/loader.gif" className="img-responsive loader"/>
                </div>
            )  
        }
    }
    return(
        <React.Fragment>
            {renderResdetail(props)}
           
            
        </React.Fragment>
    
    )
    
}
export default RestaurantDisplay