import React from 'react';
import Pagination from "react-js-pagination";
import {Link} from 'react-router-dom';


const renderList = (props,data) =>{
    if(data){
        var limit = props.limit;
        var page = props.activePage;
        data = data.slice((page - 1) * limit, (page - 1) * limit + limit);
        return data.map((item)=>{
            return(  
                <div className="list" key={item._id}>
                    <div>
                        <Link to={`/details/${item._id}`}>
                            <img src={`${item.thumb[0].image}`} alt="image" className="img-responsive listimage"/>
                        </Link>
                        <div className="listhotel">
                            <Link to={`/details/${item._id}`} className="hotelname">{item.name}</Link>
                            <h5>{item.locality}</h5>
                            <p>{item.address}</p>
                            <div><span><i className="glyphicon glyphicon-ice-lolly-tasted"></i></span>&nbsp;
                            <span><i className="glyphicon glyphicon glyphicon-cutlery"></i></span>&nbsp;&nbsp;
                            <span><i className="glyphicon glyphicon-glass"></i></span>
                            </div>
                            <div className="list-option">
                                <div className="cuisine-cost">
                                    <span>CUISINES: </span>
                                    <span>{item.Cuisine[0].name}|{item.Cuisine[1].name}</span>
                                </div>
                                <div className="cuisine-cost">
                                    <span>COST FOR TWO: </span>
                                    <span>₹{item.cost}</span>
                                </div>
                            </div>
                        </div>
                    </div>  
                </div>    
            )
        })
    }
    else{
        return(
            <div>
                <img src="/images/loader.gif" className="img-responsive loader"/>
            </div>
        )  
    }
}

const handlePageChange = (props, pageNumber) => {
    props.pageNumber(pageNumber);
    var data = props.listData;
    renderList(props, data);
}

const ListDisplay = (props) =>{
    return(
        <React.Fragment>
            <div>
                {renderList(props,props.quicklist)}
            </div>
            <center>
            <Pagination
                    activePage={props.activePage}
                    itemsCountPerPage={props.limit}
                    totalItemsCount={props.totalNoOfItems}
                    pageRangeDisplayed={5}
                    onChange={(pageNumber) => {handlePageChange(props,pageNumber)}}
                    />
            </center>
        </React.Fragment>
       
   
    )
}

export default ListDisplay


 
 
