import React, { useEffect, useState } from 'react';
import {Link} from 'react-router-dom';
import {BrowserRouter,Route} from 'react-router-dom';
import Home from './Home/Home';
import './Header.css'
import RestaurantDetails from './Details/RestaurantDetails';
import ListingApi from './List/ListingApi';
import Placeorder from './Orders/Bookingform';
import Booking from  './Orders/BookingApi';
import Footer from './Footer';
import GoogleLogin from 'react-google-login';
import Ticker from 'react-ticker';
import Cart from './Cart/Cartapi';
import Cartitem from './Cart/cartitem'


const Routing = () => {
    const [darkMode, setDarkMode] = useState(getInitialMode())
    useEffect(() => {
        sessionStorage.setItem('dark', JSON.stringify(darkMode));
    },[darkMode] );
    function getInitialMode(){
      const savedMode =  JSON.parse(sessionStorage.getItem('dark'));
      
      return savedMode || false;
    }
 
  

   const  responseGoogle = (response) => {
        return(
           alert(`welcome ${response.profileObj.name}`)
            );
      }
    return(
        <BrowserRouter >
        <div className="panel"  className="panel" className={darkMode ? "dark-mode" : "light-mode"}>
            <div className="panel-heading">
            <div >
            <nav className="navbar navbar-inverse">
                <div className="container-fluid">
                    <div className="navbar-header">
                        <button className="navbar-toggle" data-toggle="collapse" data-target="#myNav">
                            <span className="icon-bar"></span>
                            <span className="icon-bar"></span>
                            <span className="icon-bar"></span>
                        </button>
                        <Link className="navbar-brand" id="ebrand" style={{color:"#ce0505",margin:"5%"}} to="/">e!</Link>
                    </div>
                    <div className="collapse-navbar-collapse" id="myNav">
                        <ul className="nav navbar-nav">
                            <li><Link to="/" style={{color:"white"}}>Home</Link> </li>
                            <li><Link to="/vieworder"style={{color:"white"}}>Orders</Link> </li>
                            <li><Link to="/viewcart"style={{color:"white"}}>Cart</Link> </li>
                        </ul>
                        <ul className="nav navbar-nav navbar-right" style={{marginRight:"2%"}} >
                            <li>
                                <div className="dbtn">
                                    <button onClick={() => setDarkMode(PrevMode => !PrevMode)} className="darkbtn"><span className="glyphicon glyphicon-adjust"></span></button> 
                                </div>
                              
                            </li>

                            <li id="login">
                            <div style={{color:"white"}}>
                                <GoogleLogin  
                                    clientId="372967516479-a2emof4gjm7lsj77hp4619i8mcba1kjd.apps.googleusercontent.com"
                                    render={renderProps => (
                                    <button className="glogin" onClick={renderProps.onClick} disabled={renderProps.disabled}><span><i className="glyphicon glyphicon-log-in"></i></span>&nbsp;&nbsp;Login with Google</button>
                                    )}
                                    buttonText = "Login"
                                    onSuccess={responseGoogle}
                                    onFailure={responseGoogle}
                                    cookiePolicy={'single_host_origin'}
                                /> 
                            </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
            <Ticker>
                {() => (
                    <>
                        <h1 style={{fontSize:"2rem"}} >Welcome to Edumato!...What Waiting for... Hurry to taste...</h1>
                        
                    </>
                )}
            </Ticker>
        </div>
            </div>
            <div className="panel-body">
                <Route exact path='/' component={Home}></Route>
                
                <Route  path='/list/:id' className={darkMode ? "dark-mode" : "light-mode"} component={ListingApi}></Route>

                <Route  path='/details/:id' className={darkMode ? "dark-mode" : "light-mode"} component={RestaurantDetails}></Route>
                <Route path="/cart/:id" component={Cart}/>
                <Route  path='/vieworder' component={Booking}></Route>
                <Route  path='/viewcart' component={Cartitem}></Route>
                <Route  path='/booking/:id' component={Placeorder}></Route>
            </div>
           <div className="panel-footer">
                <Footer/>
           </div>
        </div>
        </BrowserRouter>

    )
}

export default Routing;