
import React, { Component } from 'react';
import './RestaurantDetails.css';
import RestaurantDisplay from './RestaurantDisplay'
import axios from 'axios'

const rdurl="https://developer-restaurant.herokuapp.com/restaurantdetails";

class RestaurantDetails extends Component {
  constructor(){
    super()
    this.state={
      Restaurantdetail:''
    }
  }
  render(){
    return(
      <React.Fragment>
        <RestaurantDisplay quickdetail={this.state.Restaurantdetail}/>
      </React.Fragment>
      );
  }

  componentDidMount(){
    var resdetail = this.props.match.params.id;
    axios.get(`${rdurl}/${resdetail}`)
    .then((response)=>{this.setState({Restaurantdetail:response.data[0]})})
  }
  
}


export default RestaurantDetails;