import React, { Component } from 'react';
import axios from 'axios';

const surl="https://developer-restaurant.herokuapp.com/restaurantList";
class Sortfilter extends Component {

   sortfiltering=(event)=>{
       console.log(event.target.value)
      let sortType=(event.target.value);
      let mealtype = sessionStorage.getItem('mealid');
     let sorturl = `${surl}/${mealtype}?sort=${sortType}`
      axios.get(sorturl)
      .then((response)=>{this.props.sortdata(response.data)})
   }



    render(){
        return(
            <React.Fragment>
                <div className="filter" onChange={this.sortfiltering}>
                    <h4>Sort</h4>
                    
                    <label className="radio">
                     <input type="radio" value='1' name="filter"/>Low to High
                    </label>
                   
                    <label className="radio"> 
                     <input type="radio" value='-1'name="filter"/>High to Low
                    </label>
                </div>
            </React.Fragment>
        )
    }
}
export default Sortfilter