import React, { Component } from 'react';
import Suggestiondisplay from './Suggestiondisplay'
import axios from 'axios';


const url="https://developer-restaurant.herokuapp.com/restaurantList";
class Suggestionapi extends Component{
    constructor(){
        super()
            this.state={
                suggestion:''
            
        }
    }
    render(){
        return(
            <Suggestiondisplay suggestdata={this.state.suggestion}/>
        )
    }
    componentDidMount(){
        
        let date = new Date().getDate();
        let mealid = sessionStorage.getItem('mealid')
        let outurl;
        if(date>0 && date <11){
            outurl=`${url}/${mealid}?lcost=500&hcost=1000`
        }else if(date>10 && date <21){
            outurl=`${url}/${mealid}?lcost=300&hcost=800`
        }else if(date>20 && date <32){
            outurl=`${url}/${mealid}?lcost=100&hcost=500`
        }
        axios.get(outurl)
        .then((response) => {this.setState({suggestion:response.data})})
    }
}
export default Suggestionapi;