import React from 'react';
import {Link} from 'react-router-dom';


const DishDisplay = (props) =>{
    const renderdish = ({quickdish}) =>{
        console.log(quickdish)
            if(quickdish){
                return quickdish.map((item) => {
        return(
        <div key={item._id}>
                    <div className="dishes">
                        <div className="dishimg">
                            <img src={`${item.image}`} alt="image" className="img-responsive dishimage"/>
                        </div>
                        <div className="dishhotel">
                            <div className="dishname" style={{textDecoration:"none"}}><h3>{item.dishname}</h3></div>
                            <p>Cost:₹{item.price}</p>
                            <p>{item.description}</p>
                            <Link to={`/cart/${item._id}`} className="btn cartbtn"><span><i class="glyphicon glyphicon-shopping-cart"></i></span></Link>
                        </div>
                    </div>
        </div>  
                )  
        })
        }
        else{
            return(
                <div>
                    <img src="/images/loader.gif" className="img-responsive loader"/>
                </div>
            )  
        }

       
    }
    return(
        <React.Fragment>
            <div>
                {renderdish(props)}
            </div>
        </React.Fragment>
        )
}
export default DishDisplay;