import React from 'react';
import './Cart.css';
import Cookie from 'js-cookie';


const CartDisplay = (props) =>{
  
    const rendercart = ({quickcart}) =>{
       
        if(quickcart){
            console.log(quickcart)
            Cookie.set("cart",JSON.stringify(quickcart))
            return(
                <div className="dishes">
                    <div className="dishimg">
                        <img src={`${quickcart.image}`} alt="image" className="img-responsive dishimage"/>
                    </div>
                    <div className="dishhotel">
                        <div style={{textDecoration:"none"}}><h3>{quickcart.dishname}</h3> </div>
                        <p>Cost:₹{quickcart.price}</p>
                        <p>{quickcart.description}</p>
                    </div>
                </div>
                
            )
            

           
        }
        
        else{
            return(
                <div>
                    <img src="/images/loader.gif" className="img-responsive loader"/>
                </div>
            )  
        } 
       
    }
   
    return(
        <React.Fragment>
            {rendercart(props)}
        </React.Fragment>
    
    )
    
}
export default CartDisplay