import React , {Component} from 'react';
import axios from 'axios';
import './Cart.css'
import CartDisplay from './Cartdisplay';

const carturl="https://developer-restaurant.herokuapp.com/dish"

class CartScreen extends Component {
    constructor(){
        super()
        this.state={
            cart:''
        }
    }
    render(){
        return(
            <div className="container">
                <CartDisplay quickcart={this.state.cart}/>
            </div>
        )
    }

    componentDidMount(){
        var cartdetail = this.props.match.params.id;
        axios.get(`${carturl}/${cartdetail}`)
        .then((response) => {this.setState({cart:response.data[0]})})
    }

}
export default CartScreen