import React, { Component } from 'react';
import axios from 'axios';
import './Dish.css'
import DishDisplay from './DishDisplay'


const rdurl="https://developer-restaurant.herokuapp.com/dishes";
class DishApi extends Component {
    constructor(){
        super()
        this.state={
          Dish:''
        }
      }
      render(){
        return(
          <React.Fragment>
            <DishDisplay quickdish={this.state.Dish}/>
          </React.Fragment>
          );
      }
    
      componentDidMount(){
        
        axios.get(`${rdurl}`)
        .then((response)=>{this.setState({Dish:response.data})})
      }
      
    }
export default DishApi;